.intro-holder {
    @include border(rgb(53, 50, 59), 1px 0 0);
    background: $pureBlack;

    .intdiv{
        margin: 70px 0 0;
    }

    .logo-prime {
        top: 50px;
        transition: all 0.5s linear 0.25s;
        width: 225px;
    }

    .botrig {
        top: 50%;
    }

    .title-wrap {
        transition: all 1s linear 0.5s;
        margin: 265px auto 0px;
    }

    .intro-p1 {
        @include fontSettings($pureWhite, 28px, 500, 1.2, 0);
        z-index: 2;
    }

    .intro-p2 {
        @include fontSettings($lightPink, 28px, 500, 1.2, 0px 0 125px);
        z-index: 2;
    }
}